

import { Direction, Directionality } from '@angular/cdk/bidi';
import { BooleanInput } from '@angular/cdk/coercion';
import { BACKSPACE, DOWN_ARROW, ENTER, ESCAPE, LEFT_ARROW, RIGHT_ARROW, UP_ARROW } from '@angular/cdk/keycodes';
import { CdkConnectedOverlay, ConnectionPositionPair } from '@angular/cdk/overlay';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  forwardRef,
  Host,
  HostListener,
  Input,
  NgZone,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  QueryList,
  Renderer2,
  TemplateRef,
  ViewChild,
  ViewChildren,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { fromEvent, Subject } from 'rxjs';
import { startWith, takeUntil } from 'rxjs/operators';
import { InputBoolean, TDSSafeAny } from 'tds-ui/shared/utility';
import { TDSCascaderI18nInterface, TDSI18nService } from 'tds-ui/i18n';


import { TDSCascaderOptionComponent } from './cascader-li.component';
import { TDSCascaderService } from './cascader.service';
import {
  TDSCascaderComponentAsSource,
  TDSCascaderExpandTrigger,
  TDSCascaderOption,
  TDSCascaderSearchOption,
  TDSCascaderSize,
  TDSCascaderTriggerType,
  TDSShowSearchOptions
} from './typings';
import { NgClassType, NgStyleInterface, TDSConfigKey, TDSConfigService, WithConfig } from 'tds-ui/core/config';
import { slideMotion } from 'tds-ui/core/animation';
import { DEFAULT_CASCADER_POSITIONS } from 'tds-ui/core/overlay';
import { TDSNoAnimationDirective } from 'tds-ui/core/no-animation';
import { toArray } from 'tds-ui/core/util';

const TDS_CONFIG_MODULE_NAME: TDSConfigKey = 'cascader';
const defaultDisplayRender = (labels: string[]): string => labels.join(' / ');

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  selector: 'tds-cascader, [tds-cascader]',
  exportAs: 'tdsCascader',
  preserveWhitespaces: false,
  templateUrl: './cascader.component.html',
  animations: [slideMotion],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TDSCascaderComponent),
      multi: true
    },
    TDSCascaderService
  ],
  host: {
    '[attr.tabIndex]': '"0"',
    '[class.tds-select-allow-clear]': 'tdsAllowClear',
    '[class.tds-select-show-arrow]': 'tdsShowArrow',
    '[class.tds-select-show-search]': '!!tdsShowSearch',
    '[class.tds-select-disabled]': 'tdsDisabled',
    '[class.tds-select-open]': 'menuVisible',
    '[class.tds-select-focused]': 'isFocused',
    '[class.tds-select-single]': 'true',
    '[class.tds-select-rtl]': `dir ==='rtl'`,
    'class': 'group'
  }
})
export class TDSCascaderComponent implements TDSCascaderComponentAsSource, OnInit, OnDestroy, ControlValueAccessor {
  readonly _tdsModuleName: TDSConfigKey = TDS_CONFIG_MODULE_NAME;
  static ngAcceptInputType_tdsShowInput: BooleanInput;
  static ngAcceptInputType_tdsShowArrow: BooleanInput;
  static ngAcceptInputType_tdsAllowClear: BooleanInput;
  static ngAcceptInputType_tdsAutoFocus: BooleanInput;
  static ngAcceptInputType_tdsChangeOnSelect: BooleanInput;
  static ngAcceptInputType_tdsDisabled: BooleanInput;

  @ViewChild('selectContainer', { static: false }) selectContainer!: ElementRef;
  @ViewChild('input', { static: false }) input!: ElementRef;
  @ViewChild('menu', { static: false }) menu!: ElementRef;
  @ViewChild(CdkConnectedOverlay, { static: false }) overlay!: CdkConnectedOverlay;
  @ViewChildren(TDSCascaderOptionComponent) cascaderItems!: QueryList<TDSCascaderOptionComponent>;

  @Input() tdsOptionRender: TemplateRef<{ $implicit: TDSCascaderOption; index: number }> | null = null;
  @Input() @InputBoolean() tdsShowInput = true;
  @Input() @InputBoolean() tdsShowArrow = true;
  @Input() @InputBoolean() tdsAllowClear = true;
  @Input() @InputBoolean() tdsAutoFocus = false;
  @Input() @InputBoolean() tdsChangeOnSelect = false;
  @Input() @InputBoolean() tdsDisabled = false;
  @Input() tdsColumnClassName?: string;
  @Input() tdsExpandTrigger: TDSCascaderExpandTrigger = 'click';
  @Input() tdsValueProperty = 'value';
  @Input() tdsLabelRender: TemplateRef<void> | null = null;
  @Input() tdsLabelProperty = 'label';
  @Input() tdsNotFoundContent?: string | TemplateRef<void>;
  @Input() @WithConfig() tdsSize: TDSCascaderSize = 'md';
  @Input() @WithConfig() tdsBackdrop = false;
  @Input() tdsShowSearch: boolean | TDSShowSearchOptions = false;
  @Input() tdsPlaceHolder: string = '';
  @Input() tdsMenuClassName?: string;
  @Input() tdsMenuStyle: NgStyleInterface | null = null;
  @Input() tdsMouseEnterDelay: number = 150; // ms
  @Input() tdsMouseLeaveDelay: number = 150; // ms
  @Input() tdsTriggerAction: TDSCascaderTriggerType | TDSCascaderTriggerType[] = ['click'] as TDSCascaderTriggerType[];
  @Input() tdsChangeOn?: (option: TDSCascaderOption, level: number) => boolean;
  @Input() tdsLoadData?: (node: TDSCascaderOption, index: number) => PromiseLike<TDSSafeAny>;
  // TODO: RTL
  @Input() tdsSuffixIcon: string | TemplateRef<void> = 'tdsi-arrow-down-line';
  @Input() tdsExpandIcon: string | TemplateRef<void> = '';

  @Input()
  get tdsOptions(): TDSCascaderOption[] | null {
    return this.cascaderService.tdsOptions;
  }

  set tdsOptions(options: TDSCascaderOption[] | null) {
    this.cascaderService.withOptions(options);
  }

  @Output() readonly tdsVisibleChange = new EventEmitter<boolean>();
  @Output() readonly tdsSelectionChange = new EventEmitter<TDSCascaderOption[]>();
  @Output() readonly tdsSelect = new EventEmitter<{ option: TDSCascaderOption; index: number } | null>();
  @Output() readonly tdsClear = new EventEmitter<void>();

  /**
   * If the dropdown should show the empty content.
   * `true` if there's no options.
   */
  shouldShowEmpty: boolean = false;

  el: HTMLElement;
  menuVisible = false;
  isLoading = false;
  labelRenderText?: string;
  labelRenderContext = {};
  onChange = Function.prototype;
  onTouched = Function.prototype;
  positions: ConnectionPositionPair[] = [...DEFAULT_CASCADER_POSITIONS];

  /**
   * Dropdown's with in pixel.
   */
  dropdownWidthStyle?: string;
  dropdownHeightStyle: 'auto' | '' = '';
  isFocused = false;

  locale!: TDSCascaderI18nInterface;
  dir: Direction = 'ltr';
  tdsClassContainer: string = "";
  tdsClassArrow: string = '';
  private destroy$ = new Subject<void>();
  private inputString = '';
  private isOpening = false;
  private delayMenuTimer: TDSSafeAny | null = null;
  private delaySelectTimer: TDSSafeAny | null = null;

  get inSearchingMode(): boolean {
    return this.cascaderService.inSearchingMode;
  }

  set inputValue(inputValue: string) {
    this.inputString = inputValue;
    this.toggleSearchingMode(!!inputValue);
  }

  get inputValue(): string {
    return this.inputString;
  }

  get menuCls(): NgClassType {
    return { [`${this.tdsMenuClassName}`]: !!this.tdsMenuClassName };
  }

  get menuColumnCls(): NgClassType {
    return { [`${this.tdsColumnClassName}`]: !!this.tdsColumnClassName };
  }

  private get hasInput(): boolean {
    return !!this.inputValue;
  }

  private get hasValue(): boolean {
    return this.cascaderService.values && this.cascaderService.values.length > 0;
  }

  get showLabelRender(): boolean {
    return this.hasValue;
  }

  get showPlaceholder(): boolean {
    return !(this.hasInput || this.hasValue);
  }

  get clearIconVisible(): boolean {
    return this.tdsAllowClear && !this.tdsDisabled && (this.hasValue || this.hasInput);
  }

  get isLabelRenderTemplate(): boolean {
    return !!this.tdsLabelRender;
  }

  constructor(
    public cascaderService: TDSCascaderService,
    public tdsConfigService: TDSConfigService,
    private ngZone: NgZone,
    private cdr: ChangeDetectorRef,
    private i18nService: TDSI18nService,
    elementRef: ElementRef,
    renderer: Renderer2,
    @Optional() private directionality: Directionality,
    @Host() @Optional() public noAnimation?: TDSNoAnimationDirective
  ) {
    this.el = elementRef.nativeElement;
    this.cascaderService.withComponent(this);
    renderer.addClass(elementRef.nativeElement, 'tds-select');
    renderer.addClass(elementRef.nativeElement, 'tds-cascader');
  }

  ngOnInit(): void {
    const srv = this.cascaderService;

    srv.$redraw.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.updateClassCss();
      // These operations would not mutate data.
      this.checkChildren();
      this.setDisplayLabel();
      this.reposition();
      this.setDropdownStyles();

      this.cdr.markForCheck();
    });

    srv.$loading.pipe(takeUntil(this.destroy$)).subscribe(loading => {
      this.isLoading = loading;
    });

    srv.$optionSelected.pipe(takeUntil(this.destroy$)).subscribe(data => {
      if (!data) {
        this.onChange([]);
        this.tdsSelect.emit(null);
        this.tdsSelectionChange.emit([]);
      } else {
        const { option, index } = data;
        const shouldClose = option.isLeaf || (this.tdsChangeOnSelect && this.tdsExpandTrigger === 'hover');
        if (shouldClose) {
          this.delaySetMenuVisible(false);
        }
        this.onChange(this.cascaderService.values);
        this.tdsSelectionChange.emit(this.cascaderService.selectedOptions);
        this.tdsSelect.emit({ option, index });
        this.cdr.markForCheck();
      }
    });

    srv.$quitSearching.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.inputString = '';
      this.dropdownWidthStyle = '';
    });

    this.i18nService.localeChange.pipe(startWith(), takeUntil(this.destroy$)).subscribe(() => {
      this.setLocale();
    });

    this.tdsConfigService
      .getConfigChangeEventForComponent(TDS_CONFIG_MODULE_NAME)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.cdr.markForCheck();
      });

    this.dir = this.directionality.value;
    this.directionality.change.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.dir = this.directionality.value;
      srv.$redraw.next();
    });

    this.setupKeydownListener();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
    this.clearDelayMenuTimer();
    this.clearDelaySelectTimer();
  }

  registerOnChange(fn: () => {}): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => {}): void {
    this.onTouched = fn;
  }

  writeValue(value: TDSSafeAny): void {
    this.cascaderService.values = toArray(value);
    this.cascaderService.syncOptions(true);
  }

  delaySetMenuVisible(visible: boolean, delay: number = 100, setOpening: boolean = false): void {
    this.clearDelayMenuTimer();
    if (delay) {
      if (visible && setOpening) {
        this.isOpening = true;
      }
      this.delayMenuTimer = setTimeout(() => {
        this.setMenuVisible(visible);
        this.cdr.detectChanges();
        this.clearDelayMenuTimer();
        if (visible) {
          setTimeout(() => {
            this.isOpening = false;
          }, 100);
        }
      }, delay);
    } else {
      this.setMenuVisible(visible);
    }
  }

  setMenuVisible(visible: boolean): void {
    if (this.tdsDisabled || this.menuVisible === visible) {
      return;
    }
    if (visible) {
      this.cascaderService.syncOptions();
      this.scrollToActivatedOptions();
    }

    if (!visible) {
      this.inputValue = '';
    }

    this.menuVisible = visible;
    this.tdsVisibleChange.emit(visible);
    this.updateClassCss();
    this.cdr.detectChanges();
  }

  private clearDelayMenuTimer(): void {
    if (this.delayMenuTimer) {
      clearTimeout(this.delayMenuTimer);
      this.delayMenuTimer = null;
    }
  }

  clearSelection(event?: Event): void {
    if (event) {
      event.preventDefault();
      event.stopPropagation();
    }

    this.labelRenderText = '';
    this.labelRenderContext = {};
    this.inputValue = '';
    this.setMenuVisible(false);
    this.cascaderService.clear();
    this.tdsClear.emit();
  }

  getSubmitValue(): TDSSafeAny[] {
    return this.cascaderService.selectedOptions.map(o => this.cascaderService.getOptionValue(o));
  }

  focus(): void {
    if (!this.isFocused) {
      (this.input ? this.input.nativeElement : this.el).focus();
      this.isFocused = true;
    }
  }

  blur(): void {
    if (this.isFocused) {
      (this.input ? this.input.nativeElement : this.el).blur();
      this.isFocused = false;
    }
  }

  handleInputBlur(): void {
    this.menuVisible ? this.focus() : this.blur();
  }

  handleInputFocus(): void {
    this.focus();
  }

  @HostListener('click')
  onTriggerClick(): void {
    if (this.tdsDisabled) {
      return;
    }
    if (this.tdsShowSearch) {
      this.focus();
    }
    if (this.isActionTrigger('click')) {
      this.delaySetMenuVisible(!this.menuVisible, 100);
    }
    this.onTouched();
  }

  @HostListener('mouseenter')
  onTriggerMouseEnter(): void {
    if (this.tdsDisabled || !this.isActionTrigger('hover')) {
      return;
    }

    this.delaySetMenuVisible(true, this.tdsMouseEnterDelay, true);
  }

  @HostListener('mouseleave', ['$event'])
  onTriggerMouseLeave(event: MouseEvent): void {
    if (this.tdsDisabled || !this.menuVisible || this.isOpening || !this.isActionTrigger('hover')) {
      event.preventDefault();
      return;
    }
    const mouseTarget = event.relatedTarget as HTMLElement;
    const hostEl = this.el;
    const menuEl = this.menu && (this.menu.nativeElement as HTMLElement);
    if (hostEl.contains(mouseTarget) || (menuEl && menuEl.contains(mouseTarget))) {
      return;
    }
    this.delaySetMenuVisible(false, this.tdsMouseLeaveDelay);
  }

  onOptionMouseEnter(option: TDSCascaderOption, columnIndex: number, event: Event): void {
    event.preventDefault();
    if (this.tdsExpandTrigger === 'hover') {
      if (!option.isLeaf) {
        this.delaySetOptionActivated(option, columnIndex, false);
      } else {
        this.cascaderService.setOptionDeactivatedSinceColumn(columnIndex);
      }
    }
  }

  onOptionMouseLeave(option: TDSCascaderOption, _columnIndex: number, event: Event): void {
    event.preventDefault();
    if (this.tdsExpandTrigger === 'hover' && !option.isLeaf) {
      this.clearDelaySelectTimer();
    }
  }

  onOptionClick(option: TDSCascaderOption, columnIndex: number, event: Event): void {
    if (event) {
      event.preventDefault();
    }
    if (option && option.disabled) {
      return;
    }
    this.el.focus();
    this.inSearchingMode
      ? this.cascaderService.setSearchOptionSelected(option as TDSCascaderSearchOption)
      : this.cascaderService.setOptionActivated(option, columnIndex, true);
  }

  onClickOutside(event: MouseEvent): void {
    if (!this.el.contains(event.target as Node)) {
      this.closeMenu();
    }
  }

  private isActionTrigger(action: 'click' | 'hover'): boolean {
    return typeof this.tdsTriggerAction === 'string'
      ? this.tdsTriggerAction === action
      : this.tdsTriggerAction.indexOf(action) !== -1;
  }

  private onEnter(): void {
    const columnIndex = Math.max(this.cascaderService.activatedOptions.length - 1, 0);
    const option = this.cascaderService.activatedOptions[columnIndex];
    if (option && !option.disabled) {
      this.inSearchingMode
        ? this.cascaderService.setSearchOptionSelected(option as TDSCascaderSearchOption)
        : this.cascaderService.setOptionActivated(option, columnIndex, true);
    }
  }

  private moveUpOrDown(isUp: boolean): void {
    const columnIndex = Math.max(this.cascaderService.activatedOptions.length - 1, 0);
    const activeOption = this.cascaderService.activatedOptions[columnIndex];
    const options = this.cascaderService.columns[columnIndex] || [];
    const length = options.length;
    let nextIndex = -1;
    if (!activeOption) {
      // Not selected options in this column
      nextIndex = isUp ? length : -1;
    } else {
      nextIndex = options.indexOf(activeOption);
    }

    while (true) {
      nextIndex = isUp ? nextIndex - 1 : nextIndex + 1;
      if (nextIndex < 0 || nextIndex >= length) {
        break;
      }
      const nextOption = options[nextIndex];
      if (!nextOption || nextOption.disabled) {
        continue;
      }
      this.cascaderService.setOptionActivated(nextOption, columnIndex);
      break;
    }
  }

  private moveLeft(): void {
    const options = this.cascaderService.activatedOptions;
    if (options.length) {
      options.pop(); // Remove the last one
    }
  }

  private moveRight(): void {
    const length = this.cascaderService.activatedOptions.length;
    const options = this.cascaderService.columns[length];
    if (options && options.length) {
      const nextOpt = options.find(o => !o.disabled);
      if (nextOpt) {
        this.cascaderService.setOptionActivated(nextOpt, length);
      }
    }
  }

  private clearDelaySelectTimer(): void {
    if (this.delaySelectTimer) {
      clearTimeout(this.delaySelectTimer);
      this.delaySelectTimer = null;
    }
  }

  private delaySetOptionActivated(option: TDSCascaderOption, columnIndex: number, performSelect: boolean): void {
    this.clearDelaySelectTimer();
    this.delaySelectTimer = setTimeout(() => {
      this.cascaderService.setOptionActivated(option, columnIndex, performSelect);
      this.delaySelectTimer = null;
    }, 150);
  }

  private toggleSearchingMode(toSearching: boolean): void {
    if (this.inSearchingMode !== toSearching) {
      this.cascaderService.toggleSearchingMode(toSearching);
    }

    if (this.inSearchingMode) {
      this.cascaderService.prepareSearchOptions(this.inputValue);
    }
  }

  isOptionActivated(option: TDSCascaderOption, index: number): boolean {
    const activeOpt = this.cascaderService.activatedOptions[index];
    return activeOpt === option;
  }

  setDisabledState(isDisabled: boolean): void {
    if (isDisabled) {
      this.closeMenu();
    }
    this.tdsDisabled = isDisabled;
  }

  closeMenu(): void {
    this.blur();
    this.clearDelayMenuTimer();
    this.setMenuVisible(false);
  }

  /**
   * Reposition the cascader panel. When a menu opens, the cascader expands
   * and may exceed the boundary of browser's window.
   */
  private reposition(): void {
    if (this.overlay && this.overlay.overlayRef && this.menuVisible) {
      Promise.resolve().then(() => {
        this.overlay.overlayRef.updatePosition();
      });
    }
  }

  /**
   * When a cascader options is changed, a child needs to know that it should re-render.
   */
  private checkChildren(): void {
    if (this.cascaderItems) {
      this.cascaderItems.forEach(item => item.markForCheck());
    }
  }

  private setDisplayLabel(): void {
    const selectedOptions = this.cascaderService.selectedOptions;
    const labels: string[] = selectedOptions.map(o => this.cascaderService.getOptionLabel(o));

    if (this.isLabelRenderTemplate) {
      this.labelRenderContext = { labels, selectedOptions };
    } else {
      this.labelRenderText = defaultDisplayRender.call(this, labels);
    }
  }

  private setDropdownStyles(): void {
    const firstColumn = this.cascaderService.columns[0];

    this.shouldShowEmpty =
      (this.inSearchingMode && (!firstColumn || !firstColumn.length)) || // Should show empty when there's no searching result
      (!(this.tdsOptions && this.tdsOptions.length) && !this.tdsLoadData); // Should show when there's no options and developer does not use tdsLoadData
    this.dropdownHeightStyle = this.shouldShowEmpty ? 'auto' : '';

    if (this.input) {
      this.dropdownWidthStyle =
        this.inSearchingMode || this.shouldShowEmpty ? `${this.selectContainer.nativeElement.offsetWidth}px` : '';
    }
  }

  private setLocale(): void {
    this.locale = this.i18nService.getLocaleData('global');
    this.cdr.markForCheck();
  }

  private scrollToActivatedOptions(): void {
    // The `scrollIntoView` is a native DOM API, which doesn't require Angular to run
    // a change detection when a promise microtask is resolved.
    this.ngZone.runOutsideAngular(() => {
      Promise.resolve().then(() => {
        // scroll only until option menu view is ready
        this.cascaderItems
          .toArray()
          .filter(e => e.activated)
          .forEach(e => {
            e.nativeElement.scrollIntoView({ block: 'start', inline: 'nearest' });
          });
      });
    });
  }

  private setupKeydownListener(): void {
    this.ngZone.runOutsideAngular(() => {
      fromEvent<KeyboardEvent>(this.el, 'keydown')
        .pipe(takeUntil(this.destroy$))
        .subscribe(event => {
          const keyCode = event.keyCode;

          if (
            keyCode !== DOWN_ARROW &&
            keyCode !== UP_ARROW &&
            keyCode !== LEFT_ARROW &&
            keyCode !== RIGHT_ARROW &&
            keyCode !== ENTER &&
            keyCode !== BACKSPACE &&
            keyCode !== ESCAPE
          ) {
            return;
          }

          // Press any keys above to reopen menu.
          if (!this.menuVisible && keyCode !== BACKSPACE && keyCode !== ESCAPE) {
            // The `setMenuVisible` runs `detectChanges()`, so we don't need to run `markForCheck()` additionally.
            return this.ngZone.run(() => this.setMenuVisible(true));
          }

          // Make these keys work as default in searching mode.
          if (this.inSearchingMode && (keyCode === BACKSPACE || keyCode === LEFT_ARROW || keyCode === RIGHT_ARROW)) {
            return;
          }

          if (!this.menuVisible) {
            return;
          }

          event.preventDefault();

          this.ngZone.run(() => {
            // Interact with the component.
            if (keyCode === DOWN_ARROW) {
              this.moveUpOrDown(false);
            } else if (keyCode === UP_ARROW) {
              this.moveUpOrDown(true);
            } else if (keyCode === LEFT_ARROW) {
              this.moveLeft();
            } else if (keyCode === RIGHT_ARROW) {
              this.moveRight();
            } else if (keyCode === ENTER) {
              this.onEnter();
            }
            // `@HostListener`s run `markForCheck()` internally before calling the actual handler so
            // we call `markForCheck()` to be backwards-compatible.
            this.cdr.markForCheck();
          });
        });
    });
  }
  //buildCss
  updateClassCss() {
    this.tdsClassContainer = this.getCssContainer();
    this.tdsClassArrow = this.getCssArrow();
  }
  private getCssBySize() {
    let result = '';
    switch (this.tdsSize) {
      case 'sm':
        result = " py-1  pl-2.5 "
        break;
      case 'md':
        result = " py-1.5  pl-3 "
        break;
      case 'lg':
        result = " py-2  pl-3.5 "
        break;
      case 'xl':
        result = " py-2.5 pl-4 "
        break;
      default:
        result = ' py-1.5  pl-3 '
    }
    return result;
  }
  private getCssArrow() {
    let result = 'text-neutral-1-900 dark:text-d-neutral-1-900 text-base leading-4 flex items-center justify-center ';

    switch (this.tdsSize) {
      case 'sm':
        result += " px-1.5 "
        break;
      case 'md':
        result += " px-2.5 "
        break;
      case 'lg':
        result += " px-3 "
        break;
      case 'xl':
        result += " px-3.5 "
        break;
      default:
        result += ' px-2.5 '
    }

    return result;
  }
  private getCssContainer() {
    let result = 'dark:bg-d-neutral-3-50 bg-white border rounded dark:group-hover:bg-d-neutral-3-50 group-hover:bg-neutral-3-50';
    if (!this.menuVisible) {
      result += " dark:border-d-neutral-2-200 border-neutral-2-200"
    } else {
      result += ' outline-none ring-opacity-20 ring ring-primary-1 border-primary-1';
    }
    result += this.tdsDisabled ? " cursor-not-allowed " : " cursor-pointer ";
    result += this.getCssBySize();
    return result;
  }
}
