

import { TDSCascaderOption } from './typings';

export function isChildOption(o: TDSCascaderOption): boolean {
  return o.isLeaf || !o.children || !o.children.length;
}

export function isParentOption(o: TDSCascaderOption): boolean {
  return !!o.children && !!o.children.length && !o.isLeaf;
}
